import React, {Component} from 'react';
import {Col, Row, Container} from 'reactstrap';
import Header from '../header/header.jsx';
import RandomChar from '../randomChar/randomChar.jsx';
import ItemList from '../itemList/itemList.jsx';
import CharDetails from '../charDetails/charDetails.jsx';

import style from './app.module.css';

export default class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            charShow: true,
            selectedChar: 130
        }
        this.changeCharElement = this.changeCharElement.bind(this);
        this.onCharSelected = this.onCharSelected.bind(this);
    }

    changeCharElement() {
        this.setState((state) => {
            return {charShow: !state.charShow};
        });
    }

    onCharSelected(id) {
        this.setState({
            selectedChar: id
        });
        console.log(id);
    }

    render() {
        const charShow = this.state.charShow ? <RandomChar/> : null;

        return (
            <> 
                <Container>
                    <Header />
                </Container>
                <Container>
                    <Row>
                        <Col lg={{size: 5, offset: 0}}>
                            {charShow}
                            <button 
                                onClick={this.changeCharElement}
                                className={style["hideChar"]}>Toggle random character</button>
                        </Col>
                    </Row>
                    <Row>
                        <Col md='6'>
                            <ItemList onCharSelected={this.onCharSelected}/>
                        </Col>
                        <Col md='6'>
                            <CharDetails charId={this.state.selectedChar}/>
                        </Col>
                    </Row>
                </Container>
            </>
        )
    }
};