export default class GotService {
    constructor() {
        this._apiBase = 'https://www.anapioficeandfire.com/api';
    }
    async getResourse(url) {
        const res = await fetch(`${this._apiBase}${url}`);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`);
        }

        return await res.json();
    }

    async getAllCharacters() {
        const res = await this.getResourse(`/characters?page=5&pageSize=10`);
        return res.map(this._transformCharacter);
    }

    async getCharacter(id) {
        const res = await this.getResourse(`/characters/${id}`);
        return this._transformCharacter(res)
    }

    async getAllHouses() {
        const res = await this.getResourse(`/houses/`);
        return res.map(this._transformHouse);
    }

    async getHouse(id) {
        const res = await this.getResourse(`/houses/${id}`);
        return this._transformHouse(res);
    }

    async getAllBooks() {
        const res = await this.getResourse(`/books/`);
        return res.map(this._transformBook);
    }

    async getBook(id) {
        const res = await this.getResourse(`/books/${id}`);
        return this._transformBook(res);
    }

    _transformCharacter(char) {
        const newPerson = {}
        const keys = ['name', 'gender', 'born', 'died', 'culture'];
        keys.forEach(el => {
            if (char[el]) {
                newPerson[el] = char[el]
            } else {
                newPerson[el] = 'no data';
            }
        });
        return newPerson;
    }

    _transformHouse(house) {
        return {
            name: house.name,
            region: house.region,
            words: house.words,
            titles: house.titles,
            overlord: house.overlord,
            ancestralWeapons: house.ancestralWeapons,
        }
    }

    _transformBook(book) {
        return {
            name: book.name,
            numberOfPages: book.numberOfPages,
            publiser: book.publiser,
            released: book.released,
        }
    }
}