import React, {Component} from 'react';
import GotService from '../../service/gotService';
import ErrorMessage from '../errorMessage/errorMessage';
import Spinner from '../spinner/spinner';
import style from './randomChar.module.css';

export default class RandomChar extends Component {
    gotService = new GotService();
    constructor(props) {
        super(props);
        this.state = {
            char: {},
            loading: true
        }
        this.updateChar = this.updateChar.bind(this);
        this.onCharLoaded = this.onCharLoaded.bind(this);
    }

    onCharLoaded = (char) => {
        this.setState({
            char,
            loading: false,
            error: false
        });
    }
    
    componentDidMount() {
        this.updateChar();
        this.timerID = setInterval(this.updateChar, 1500);
        console.log('mounting');
    }
    
    componentWillUnmount() {
        clearInterval(this.timerID);
        console.log('unmounting');
    }

    onError = (err) => {
        this.setState({
            error: true,
            loading: false
        });
    }

    updateChar = () => {
        const id = Math.floor(Math.random() * 140 + 25);
        this.gotService.getCharacter(id)
            .then(this.onCharLoaded)
            .catch(this.onError)
    }

    render() {
        console.log('render');
        const {char, loading, error} = this.state;
        const errorMessage = error ? <ErrorMessage/> : null;
        const spinner = loading ? <Spinner/> : null;
        const content = !(loading || error) ? <View char={char}/> : null;

        return (
            <div className={style["random-block"] + " rounded"}>
                {errorMessage}
                {spinner}
                {content}
            </div>
        );
    }
}

const View = ({char}) => {
    const {name, gender, born, died, culture} = char;
    return (
        <>
            <h4>Random Character: {name}</h4>
            <ul className="list-group list-group-flush">
                <li className="list-group-item d-flex justify-content-between">
                    <span className={style["term"]}>Gender </span>
                    <span>{gender}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between">
                    <span className={style["term"]}>Born </span>
                    <span>{born}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between">
                    <span className={style["term"]}>Died </span>
                    <span>{died}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between">
                    <span className={style["term"]}>Culture </span>
                    <span>{culture}</span>
                </li>
            </ul>
        </>
    )
};
